<template>
<div class="wrapper">
	<Message-box :messageBoxEvent="this.messageBox.messageBoxEvent" :visible="this.messageBox.visible" :hasCancel="this.messageBox.hasCancel" @cancel="cancel" @confirm="confirm">
		<p slot="content">{{this.messageBox.message}}</p>
	</Message-box>
	<Header goback='true' chatTitle=""></Header>
  <div class="chat-wrapper-spe">
    <div class="secret-box-spe">
      <p></p>
      <textarea rows="5" v-model="textAreaContent" maxlength="60"></textarea>
      <div class="action" @click="send">
        <span class="primary-span"> </span>
      </div>
      <!--<p class="send-btn" @click="send"></p>-->
    </div>
  </div>

</div>
</template>

<script>
import Header from '../components/Header.vue'
import { mapActions } from 'vuex';
import { toNomalTime } from "../utils/common"
export default {
	components: {
		Header
	},
	data() {
		return {
			textAreaContent: "",
			messageBox: {
				visible: false,
				message: "", // 
				hasCancel: true, // 
				messageBoxEvent: "" // 
			},
			fromUserInfo: {}
		}
	},
	computed: {
		//  ...mapGetters([
		//      'addAsFriendGetter'
		//  ])
	},

	watch: {},

	methods: {
    ...mapActions(["insertNewFriends"]),
		send() {
      socketWeb.emit('sendRequest', {
				from_user: this.fromUserInfo.user_id,
				to_user: this.$route.params.user_id, // id
				name: this.fromUserInfo.name,
				avatar: this.fromUserInfo.avatar,
				sex: this.fromUserInfo.sex,
				content: this.textAreaContent,
				time: toNomalTime((new Date()).getTime()) // 
			})
			// db
      let params = {
        to_user: this.$route.params.user_id, // id
        content: this.textAreaContent,
        time: toNomalTime((new Date()).getTime()), // 
        status: 0
      }
      this.insertNewFriends(params).then(res => {
        if (res) {
          this.messageBox.messageBoxEvent = 'send'
          this.messageBox.visible = true;
          this.messageBox.message = ""
        }
      })
		},
		confirm(value) {
			if (value === 'send') {
				const path = `/message`
				this.$router.push(path)
			}
		},
		cancel(value) {
			this.messageBox.visible = false;
		}
	},

	mounted() {
		this.fromUserInfo = JSON.parse(localStorage.getItem("HappyChatUserInfo"));
	}
}
</script>

<style lang="scss" scoped>
.wrapper {
    text-align: center;
    textarea {
        width: 90%;
        margin: 0.3rem auto;
        padding: 1.5vh;
        font-size: 0.26rem;
        /**/
        outline: none;
        /**/
        resize: none;
    }
    p {
        font-size: 0.32rem;
        margin: 0.2rem 0;
    }
    .send-btn {
        background-color: #4290F7;
        color: #fff;
        display: inline-block;
        font-size: 0.2rem;
        line-height: 0.26rem;
        padding: 0.16rem 0;
        width: 40%;
        cursor: pointer;
    }
}
</style>



