<template>
<div class="wrapper">
	<Header goback='true' chatTitle=""></Header>
  <div class="chat-wrapper-no-footer">
    <div class="secret-box">
      <ul>
        <Nothing v-if="newFriendGetter.length === 0" :name="''" :type="'addAuthor'"></Nothing>
        <li else v-for="data in newFriendGetter">
          <div class="list-box" @click="enterIt(data.from_user)">
            <img :src="data.avatar" alt="">
            <div class="content">
              <p>{{data.name}}</p>
              <p>{{data.content}}</p>
            </div>
          </div>

          <div class="result">
            <span v-if="data.status === 0" class="agree-btn" @click="agreeBeFriend(data.from_user)"></span>
            <span v-if="data.status === 1"></span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
import Header from '../components/Header.vue'
import axios from "axios"
import { mapGetters, mapActions } from 'vuex';
import { toNomalTime } from "../utils/common"
import Nothing from '../components/Nothing.vue'
export default {
	components: {
		Header,
    Nothing
	},

	data() {
		return {
			userInfo: {},
			time: ""
		}
	},
	computed: {
		...mapGetters([
			'newFriendGetter'
		])
	},

	watch: {},

	methods: {
    ...mapActions(["getNewFriends","beFriends","updateNewFriendsState"]),
		enterIt(val) {
			this.$router.push(`/user_info/${val}`)
		},
		// 
		async agreeBeFriend(val) {
			this.beFriends({
        other_user_id: val,
        time: toNomalTime((new Date()).getTime()) // 
      })
      this.updateNewFriends(val);
		},
		// 
		async updateNewFriends(val) {
      this.updateNewFriendsState({ from_user: val })
			let data = {};
			this.newFriendGetter.forEach((ele) => {
				if (ele.from_user == val) {
					ele.status = 1;
					data = {
						avatar: ele.avatar, // 
						id: val, // id
						other_user_id: val,
						message: "",
						time: toNomalTime((new Date()).getTime()), // 
						name: ele.name, // 
						type: "private",
						action: "push"
					}
				}
			})
			this.$store.commit('updateListMutation', data)
			const data2 = {
				from_user: this.userInfo.user_id, // id
				to_user: val,
				name: this.userInfo.name, // 
				avatar: this.userInfo.avatar, // 
				message: data.message, // 
				type: 'private',
				action: "request",
				status: '1', //  0 1
				time: toNomalTime((new Date()).getTime()) // 
			};
      socketWeb.emit('sendPrivateMsg', data2); // 

		}
	},
	created() {
		this.userInfo = JSON.parse(localStorage.getItem("HappyChatUserInfo"));
    this.$store.commit('friendReqTipsMutation', false);
    this.getNewFriends({user_id: this.userInfo.user_id});
	}
}
</script>

<style lang="scss" scoped>
.wrapper {
  height: 100vh;
  min-width: 100%;
  width: 100%;
  position: relative;
.chat-wrapper{
  height: calc(100% - 2rem);
  width: 100%;
  position: relative;
}
.secret-box {
  height: 100%;
  padding: 0px 0px 20px 0;
  display: flex;
  flex-direction: column;
  list-style: none;
  overflow-y: auto;
}

.list-box{
      padding: 1.5vh 2vh;
      display: flex;
      justify-items: center;
      align-items: center;
    }
    ul {
        margin-top: 0.2rem;
        li {
            cursor: pointer;
            background-color: #fff;
            list-style-type: none;
            margin-bottom: 0.16rem;
            position: relative;
            img {
                width: 0.8rem;
                height: 0.8rem;
            }
            .content {
                display: inline-block;
                margin-left: 2vh;
                line-height: 3vh;
                p:nth-child(1) {
                    font-size: 0.24rem;
                }
                p:nth-child(2) {
                    font-size: 0.2rem;
                }
            }
            .result {
                z-index: 999;
                span {
                    font-size: 0.2rem;
                    position: absolute;
                    right: 0.3rem;
                    //  top: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }
                .agree-btn {
                    color: #fff;
                    background-color: #1E90FF;
                    padding: 0.1rem 0.2rem;
                    border-radius: 0.03rem;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>



