<template>
<!--   -->
<div class="login">
	<Message-box :visible="this.messageBox.visible" :messageBoxEvent="this.messageBox.messageBoxEvent" @confirm="confirm" :hasCancel=false>
		<p slot="content">{{this.messageBox.message}}</p>
	</Message-box>
	<div class="wrapper fadeInDown">
		<div id="formContent">
      <div class="flex-style">
        <h2 class="inactive" @click="goLogin()"> &nbsp;&nbsp; </h2>
        <h2 class="active">&nbsp;&nbsp; </h2>
      </div>
			<div class="fadeIn first">
        <svg id="icon" class="icon-logo" alt="happyChat" title="happyChat" aria-hidden="true">
          <use xlink:href="#iconliaotian-copy-copy-copy"></use>
        </svg>
        <div class="system-name">{{systemName}}</div>
			</div>
			<form class="register-form">
				<div class="login-form-flex">
          <span class="normal-word"></span><input v-focus style="-webkit-user-select:text !important" maxlength="16" type="text" class="input-class fadeIn second" v-model="name" placeholder="">
        </div>
        <div class="login-form-flex">
          <span class="normal-word">&nbsp;&nbsp;&nbsp;</span><input style="-webkit-user-select:text !important" maxlength="24" type="password" class="input-class fadeIn third" v-model="password" placeholder="">
        </div>
				<div class="login-form-flex">
          <span class="normal-word">&nbsp;&nbsp;&nbsp;</span><input style="-webkit-user-select:text !important" maxlength="36" type="email" class="input-class fadeIn third" v-model="email" placeholder="">
        </div>
        <div class="action action-box-spe" :class="{'disabled': disabledFlag}">
          <span  @click="startRegister" class="primary-span"> </span>
        </div>
				<!--<input type="button" :disabed="disabledFlag" @click="startRegister" class="fadeIn fourth" value="">-->
			</form>
		</div>
	</div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { checkEmail } from "../utils/common"
export default {
	data() {
		return {
			name: '',
			password: '',
      email: '',
			messageBox: {
				visible: false,
				message: "", // 
				hasCancel: true, // 
				messageBoxEvent: "" //  
			},
      disabledFlag: false
		}
	},
  computed: {
    ...mapGetters([
      'systemName'
    ])
  },
	methods: {
    ...mapActions(["register", "activateEmail"]),
	  actualRegister() {
	    let params = {
	      name: this.name,
        password: this.password,
        email: this.email
      }
      if (this.name !== "" && this.password !== "" && this.email !== "" && checkEmail(this.email)) {
        this.register(params).then(res => {
          if (res) {
            if (res.success) {
              // 
              this.messageBox.messageBoxEvent = 'register'
              this.messageBox.visible = true;
              this.messageBox.message = res.message || "";
            } else {
              this.$message({
                message: res.message,
                type: "error"
              });
            }
          }
          this.disabledFlag = false;
        }).catch(err => {
          console.log(err)
          this.$message({
            message: '',
            type: "error"
          });
          this.disabledFlag = false;
        })
      } else {
        let message;
        if (this.name === "") { message = "" }
        if (this.password === "") { message = "" }
        if (this.email === "") { message = "" }
        if (!checkEmail(this.email)) { message = "" }
        this.$message({
          message: message,
          type: "warn"
        });
      }
    },
    startRegister() {
      this.disabledFlag = true;
      this.actualRegister()
		},
		confirm(value) {
			if (value === 'register') {
				this.messageBox.visible = false;
				this.$router.push("/login");
			}
		},
    goLogin() {
      this.$router.push({ path: '/login' });
    }
	}
}
</script>

<style lang="scss" scoped>
@import '../assets/css/loginregister.scss';
</style>



