<template>
<!--  MessageBox -->
<div class="message-box" v-if="visible">
	<div class="bg"></div>
	<div class="message-box-wrapper">
		<h1> {{title}}</h1>
		<input v-if="canInput" type="text" v-model="canInputText" maxlength="10" placeholder="10">
		<form class="editor-info" v-if="messageBoxEvent === 'editorInfo'">
			<div class="">
        <!--<span style="font-size: 12px">{{myInfo}}</span>-->
				<span>github:</span><input type="text" maxlength="40" v-model="myInfoSpe.github">
			</div>
			<div class="">
				<span>website:</span><input type="text" maxlength="40" v-model="myInfoSpe.website">
			</div>
			<div class="info-sex">
				<span>:</span>
				<select v-model="myInfoSpe.sex">
			   <option class="option" disabled value=""></option>
			   <option class="option" value="1"></option>
			   <option class="option" value="0"></option>
			 </select>
			</div>
			<div class="">
				<span>:</span><input type="text" v-model="myInfoSpe.place" maxlength="20">
			</div>
		</form>
    <form class="editor-info" v-if="messageBoxEvent === 'editorPassword'">
      <div class="">
        <span>:</span><input type="text" maxlength="24" v-model="password.old">
      </div>
      <div class="">
        <span>:</span><input type="text" maxlength="24" v-model="password.new">
      </div>
      <div class="">
        <span>:</span><input type="text" maxlength="24" v-model="password.confirmNew">
      </div>
    </form>
		<p v-else class="content">
			<slot name="content"></slot>
		</p>
		<div v-if="hasCancel" class="hasCancel">
			<p @click="cancel"></p>
			<p @click="confirm"></p>
		</div>
		<div v-else class="noCancel">
			<p @click="confirm"></p>
		</div>
	</div>


</div>
</template>

<script>
export default {
	name: 'MessageBox',
	props: {
		messageBoxEvent: {
			type: String,
      default: ""
		},
		visible: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: ""
		},
		// 
		canInput: {
			type: Boolean,
			default: false
		},
		// 
		canEditorInfo: {
			type: Boolean,
			default: false
		},
		hasCancel: {
			type: Boolean,
			default: true
		},
    myInfoSpe: {
			type: Object,
      default: () => ({})
		},
    clear: {
      type: Boolean,
      default: false
    }
	},
	components: {},
  mounted() {

  },
	data() {
		return {
			canInputText: "",
      password: {
			  old: '',
        new: '',
        confirmNew: ''
      }
		};
	},

	computed: {},

	watch: {
    clear(value) {
      this.password = {}
    }
  },

	methods: {
		cancel() {
      this.password = {}
			this.$emit("cancel", this.messageBoxEvent);
		},
		confirm() {
			if (this.canInput) {
				this.$emit("confirm", {
					messageBoxEvent: this.messageBoxEvent,
					canInputText: this.canInputText
				});
				return
			} else if (this.messageBoxEvent  === 'editorInfo') {
				this.$emit("confirm", {
					messageBoxEvent: this.messageBoxEvent,
					myInfo: JSON.parse(JSON.stringify(this.myInfoSpe))
				});
				return
			} else if (this.messageBoxEvent === 'editorPassword') {
        this.$emit("confirm", {
          messageBoxEvent: this.messageBoxEvent,
          password: this.password
        });
        return
      }
			this.$emit("confirm", this.messageBoxEvent);
		}
	}
}
</script>

<style lang="scss" scoped>
.bg {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.3;
    z-index: 100;
}

.message-box-wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    // Mozillafirefox3.5+
    -webkit-transform: translate(-50%, -50%); // WebkitSafari and Chrome
    -o-transform: translate(-50%, -50%);
    // Opera
    -ms-transform: translate(-50%, -50%);
    // IE9
    width: 80%;
    height: auto;
    background-color: white;
    border-radius: 0.1rem;
    border-top: 0.01rem solid white;
    animation: fade 1s ease 1 forwards;
    text-align: center;
    z-index: 999;
    @keyframes fade {
        from {
            opacity: 0;
            transform: translate(-50%, -50%) scale(0.8);
        }
        to {
            opacity: 1;
            transform: translate(-50%, -50%) scale(1);
        }
    }
    h1 {
        text-align: center;
        font-size: 0.34rem;
        margin: 0.16rem 0.01rem 0.2rem;
    }
    p {
        font-size: 0.24rem;
        color: rgba(0, 0, 0, 0.808);
        margin-bottom: 0.18rem;
    }
    /*IE10+, cssinput*/
    ::-ms-reveal,
    input[type=text]::-ms-clear {
        display: none;
    }
    ::-ms-reveal,
    input::-ms-clear {
        display: none;
    }
    input {
        //  display: inline-block;
        /**/
        width: 66%;
        outline: none;
        border:: 0.01rem solid #fff;
        border-bottom:: 0.01rem solid #999;
        padding: 0.06rem 0 0.1rem 0.1rem;
        font-size: 0.28rem;
        -webkit-appearance: none;
        /**/
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        /**/
        /*paddingpadding*/
    }
    .hasCancel {
        height: 0.8rem;
        p {
            background: transparent;
            color: #1678E5;
            font-size: 0.28rem;
            height: 0.8rem;
            line-height: 0.8rem;
            width: 50%;
            position: absolute;
        }
        p:nth-of-type(1) {
            border-top: 0.01rem solid #B4B4B4;
            border-right: 0.01rem solid #B4B4B4;
            left: 0;
            border-radius: 0 0 0 0.1rem;
        }
        p:nth-of-type(2) {
            border-top: 0.01rem solid #B4B4B4;
            right: 0;
            border-radius: 0 0 0.1rem 0;
        }
        p:focus,
        p:hover {
            font-weight: bold;
            background: #EFEFEF;
        }
        p:active {
            background: #D6D6D6;
        }
    }
    .noCancel {
        height: 0.8rem;
        p {
            background: transparent;
            color: #1678E5;
            height: 0.8rem;
            font-size: 0.24rem;
            line-height: 0.8rem;
            width: 100%;
            position: absolute;
            text-decoration: none;
            border-top: 1px solid #B4B4B4;
            left: 0;
            right: 0;
            border-radius: 0 0 0 0.1rem;
        }
        p:focus,
        p:hover {
            font-weight: bold;
            background: #EFEFEF;
        }
        p:active {
            background: #D6D6D6;
        }
    }
    .editor-info {
        display: flex;
        display: -ms-flexbox;
        flex-direction: column;
        justify-content: center;
        font-size: 0.28rem;
        margin-bottom: 0.24rem;
        div {
            width: 100%;
            margin: 0.3rem auto;
            select {
                width: 40%;
                font-size: 0.28rem;
                .option{
                  font-size: 0.28rem;
                }
            }
            input {
                /*font-size: 0.1rem;*/
            }
        }
    }
}
</style>



