//  The Vue build version to load with the `import` command
//  (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import "@/assets/css/index.scss"
import VueClipboard from 'vue-clipboard2'
import Components from './components/index'
import Loading from './components/Loading/index'

/*  */
import * as fliterCommon from '../src/utils/common'
Object.keys(fliterCommon).forEach(key => Vue.filter(key, fliterCommon[key]));

/* focus */
Vue.directive('focus', {
  inserted: function (el) {
    el.focus();
  }
})

/* scrollscroll */
import scrollDirective from '../src/utils/scroll';
Vue.directive('scroll', scrollDirective)

Vue.use(Components);

Vue.use(Loading)

Vue.use(VueClipboard)

Vue.config.productionTip = false;

/*PV*/
router.beforeEach((to, from, next) => {
  let flag = localStorage.getItem("HappyChatUserInfo") !== null ? true: false;
  let data = {
    type: 'visit',
    user_id: flag ? JSON.parse(localStorage.getItem("HappyChatUserInfo")).user_id: 'userId',
    time: (new Date()).getTime(),
    params: {
      from: {
        name: from.name || '',
        path: from.path || '',
        query: from.query || ''
      },
      to: {
        name: to.name || '',
        path: to.path || '',
        query: to.query || ''
      }
    }
  }
  App.methods.logEvent(data);
  next()
})

// window.onbeforeunload = function (e) {
//   e = e || window.event;
//   // IE8Firefox 4
//   if (e) {
//     e.returnValue = '';
//     alert("");
//   }
//   // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
//   return '';
// };
window.onunload = function unloadPage() {
  let data = {
    type: 'close',
    user_id: localStorage.getItem("HappyChatUserInfo") !== null ? JSON.parse(localStorage.getItem("HappyChatUserInfo")).user_id: 'userId',
    time: (new Date()).getTime(),
    params: {
      from: {
        name: '',
        path: router.currentRoute.path || '',
        query: router.currentRoute.params || ''
      },
      to: {
        name: '',
        path: '',
        query: ''
      }
    }
  }
  App.methods.logEvent(data);
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



